  box-sizing: border-box;
}

html {
  height: 100vh;
  font-family: "TASA Orbiter", sans-serif;
  font-weight: 400;
}

body {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #141414;
  color: #fff;
}

.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 4rem;
  background-color: #ffee00;
  z-index: 1000;
  border-bottom: 1px solid #ffee00;
}

.navbar-main-link {
  display: flex;
  justify-content: flex-start;
  padding: 1rem;
  margin-left: 2rem;
}
.navbar-main-link .navbar-brand {
  font-size: 1.5rem;
  font-weight: 600;
  color: #fff;
  text-decoration: none;
  font-family: "Lato", sans-serif;
  color: #141414;
  animation: slideInDown;
  animation-duration: 1s;
}
.navbar-main-link .navbar-brand:hover {
  color: #fff;
}

.main-content {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  margin-top: 5rem;
}

.book-form-input {
  width: 80%;
  max-width: 600px;
  padding: 1.5rem;
  border: 1px solid #ffd500;
  border-radius: 1rem;
}
.book-form-input h2 {
  font-size: 1.5rem;
  font-weight: 600;
  color: #fff;
  animation: bounce;
  animation-duration: 2s;
  margin-bottom: 1rem;
  text-align: center;
}
.book-form-input h2:hover {
  color: #ffb300;
}

#book-form {
  display: grid;
  flex-direction: column;
  align-items: center;
  align-content: center;
  justify-content: center;
  grid-template-columns: 1fr;
  margin: 1rem auto;
  width: 90%;
  gap: 0.8rem;
}
#book-form label {
  font-size: 1.2rem;
  font-weight: 400;
  color: #fff;
  text-align: left;
  display: block;
}
#book-form input {
  display: block;
  width: 100%;
  padding: 1rem;
  height: 2rem;
  border: none;
  border-bottom: 2px solid #ffd500;
  background-color: #141414;
  font-family: "Lato", sans-serif;
  color: #fff;
  font-size: 1.1rem;
}
#book-form input:focus {
  outline: 4px solid #ffd500;
  border: 1px solid #ffb300;
  background-color: #fff;
  color: #141414;
}
#book-form button {
  width: 80%;
  padding: 0.7rem;
  border: none;
  border-radius: 10px;
  background-color: #ffee00;
  margin: 1rem auto;
  font-size: 1.2rem;
  color: #141414;
  font-family: "Lato", sans-serif;
  font-weight: 600;
}
#book-form button:hover {
  background-color: #141414;
  color: #ffd500;
  border: 1px solid #ffb300;
  animation: pulse;
  animation-duration: 0.5s;
}

h1 {
  animation: bounce;
  animation-duration: 2s;
  font-size: 4rem;
}`, "",{"version":3,"sources":["webpack://./frontend/styles/index.scss"],"names":[],"mappings":"AAYA;EACI,sBAAA;AARJ;;AAWA;EACI,aAAA;EACA,uCAAA;EACA,gBAAA;AARJ;;AAWA;EACI,aAAA;EACA,sBAAA;EACA,aAAA;EACA,yBArBmB;EAsBnB,WArBe;AAanB;;AAWA;EACI,eAAA;EACA,MAAA;EACA,OAAA;EACA,WAAA;EACA,YAAA;EACA,yBA5BkB;EA6BlB,aAAA;EACA,gCAAA;AARJ;;AAWA;EACI,aAAA;EACA,2BAAA;EACA,aAAA;EACA,iBAAA;AARJ;AASI;EACI,iBAAA;EACA,gBAAA;EACA,WA3CW;EA4CX,qBAAA;EACA,+BAAA;EACA,cA/Ce;EAgDf,sBAAA;EACA,sBAAA;AAPR;AAQQ;EACI,WAlDO;AA4CnB;;AAWA;EACI,aAAA;EACA,uBAAA;EACA,mBAAA;EACA,YAAA;EACA,WAAA;EACA,gBAAA;AARJ;;AAWA;EACI,UAAA;EACA,gBAAA;EACA,eAAA;EACA,yBAAA;EACA,mBAAA;AARJ;AAUI;EACI,iBAAA;EACA,gBAAA;EACA,WA1EW;EA2EX,iBAAA;EACA,sBAAA;EACA,mBAAA;EACA,kBAAA;AARR;AAUQ;EACI,cA7EQ;AAqEpB;;AAaA;EACI,aAAA;EACA,sBAAA;EACA,mBAAA;EACA,qBAAA;EACA,uBAAA;EACA,0BAAA;EACA,iBAAA;EACA,UAAA;EACA,WAAA;AAVJ;AAYI;EACI,iBAAA;EACA,gBAAA;EACA,WApGW;EAqGX,gBAAA;EACA,cAAA;AAVR;AAaI;EACI,cAAA;EACA,WAAA;EACA,aAAA;EACA,YAAA;EACA,YAAA;EACA,gCAAA;EACA,yBAjHe;EAkHf,+BAAA;EACA,WAlHW;EAmHX,iBAAA;AAXR;AAYQ;EACI,0BAAA;EACA,yBAAA;EACA,sBAvHO;EAwHP,cAzHW;AA+GvB;AAcI;EACI,UAAA;EACA,eAAA;EACA,YAAA;EACA,mBAAA;EACA,yBA/Hc;EAgId,iBAAA;EACA,iBAAA;EACA,cArIe;EAsIf,+BAAA;EACA,gBAAA;AAZR;AAaQ;EACI,yBAzIW;EA0IX,cAtIG;EAuIH,yBAAA;EACA,gBAAA;EACA,wBAAA;AAXZ;;AAgBA;EACI,iBAAA;EACA,sBAAA;EACA,eAAA;AAbJ","sourcesContent":["@use \"./fonts\";\n@import \"~animate.css\";\n\n\n// Colors\n$dark-backgroud-color: #141414;\n$light-text-color: #fff;\n$gray-color: #c5c5c5;\n$navbar-border-color: #ffee00;\n$border-color: #ffd500;\n$form-border-color: #ffb300;\n\n* {\n    box-sizing: border-box;\n}\n\nhtml {\n    height: 100vh;\n    font-family: \"TASA Orbiter\", sans-serif;\n    font-weight: 400;\n}\n\nbody {\n    display: flex;\n    flex-direction: column;\n    height: 100vh;\n    background-color: $dark-backgroud-color;\n    color: $light-text-color;\n}\n\n.navbar {\n    position: fixed;\n    top: 0;\n    left:0;\n    width: 100%;\n    height: 4rem;\n    background-colo